.splash {
  background:
    linear-gradient(50deg,
      rgb(255, 173, 173),
      rgb(255, 214, 163),
      rgb(253, 255, 184),
      rgb(200, 255, 189),
      rgb(153, 246, 255),
      rgb(158, 195, 255),
      rgb(190, 179, 255),
      rgb(255, 199, 255),
      rgb(255, 255, 250));
  background-size: 500% 500%;
  animation: bg-anim 20s ease-in-out infinite alternate;
}

@keyframes bg-anim {
  0% {
    background-position: 0% 100%;
  }

  100% {
    background-position: 100% 0%;
  }
}
